@import url('https://fonts.googleapis.com/css?family=Geo');
@import url('https://fonts.googleapis.com/css?family=Wendy+One');
@import url('https://fonts.googleapis.com/css?family=Press+Start+2P');

li {
  background-color: rgba(255, 255, 255, 0.67);
  border-bottom: 1px solid black;
  border-right: 1px solid;
  border-radius: 5px;
}

.app-header-bot {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: baseline;
      align-items: baseline;
  background: -webkit-gradient(linear, 
   left top, left bottom, 
   from(rgba(136,234,231,1)),
   color-stop(44%, rgba(239,239,239,0.84)),
   color-stop(84%, rgba(242,176,244,0.69)),
   to(rgba(113, 94, 113, 0.63)));
  background: -webkit-linear-gradient(top, 
   rgba(136,234,231,1) 0%,
   rgba(239,239,239,0.84) 44%,
   rgba(242,176,244,0.69) 84%,
   rgba(113, 94, 113, 0.63) 100%);
  background: -o-linear-gradient(top, 
   rgba(136,234,231,1) 0%,
   rgba(239,239,239,0.84) 44%,
   rgba(242,176,244,0.69) 84%,
   rgba(113, 94, 113, 0.63) 100%);
  background: linear-gradient(to bottom, 
   rgba(136,234,231,1) 0%,
   rgba(239,239,239,0.84) 44%,
   rgba(242,176,244,0.69) 84%,
   rgba(113, 94, 113, 0.63) 100%);
  -webkit-box-shadow: 0px 9px 10px 0px rgba(0, 0, 0, 0.58);
          box-shadow: 0px 9px 10px 0px rgba(0, 0, 0, 0.58);
  padding: 5px 0px 12px 3px;  
}

.bot--userArea {
  margin-left: 5px;
  background: #f5eeee;
  padding: 15px;
  border-bottom: 3px solid #988cd8;
  border-radius: 11px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.bot--userArea-nameBox {
  margin: 0;
  border: 1px solid rgba(228, 55, 55, 0.28);
  padding: 5px 2px;
  outline-color: #b2f7fa;
  border-radius: 3px;  
  -webkit-transition: 0.3s;  
  -o-transition: 0.3s;  
  transition: 0.3s;
}

.bot--userArea-marquee {
  background-color: #f0f8ff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: large;
  width: 194px;
  font-family: 'Press Start 2P', cursive;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.bot--userArea-name {
  border: 3px solid #b7b2d2;
  padding: 5px;
  margin-right: 5px;
  background-color: #006297;
  color: white;
}

.bot--userArea-btn {
  padding: 5px 6px;
  color: white;
  margin: 0 3px;
  background-color: #988cd8;
  font-weight: lighter;
  letter-spacing: 3px;
  -webkit-text-stroke: 1px white;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
  -webkit-box-shadow: 4px 4px #d0d0d0;
          box-shadow: 4px 4px #d0d0d0;
}

.bot--userArea-btn:hover {
  background-color: #5F5599;
}

.bot--userArea-btn:active {
  background-color: #5F5599;
  -webkit-box-shadow: 2px 2px #9E9E9E;
          box-shadow: 2px 2px #9E9E9E;
  -webkit-transform: translateY(2px) translateX(2px);
      -ms-transform: translateY(2px) translateX(2px);
          transform: translateY(2px) translateX(2px);
}

.bot--botArea {
  background: rgba(1, 218, 230, 0.32);
  display: -ms-flexbox;
  display: flex;
  padding: 11px;
  margin-right: 5px;
  -ms-flex-align: baseline;
      align-items: baseline;
  border: 5px solid rgba(0, 0, 0, 0.4);
  border-radius: 15px;
}

.bot--botArea-name {
  background: #31c4c5;
  border-bottom: 2px solid black;
  padding: 7px;
}

.bot--botArea-message {
   position: relative;
   word-wrap: break-word;
   line-height: 72px;
   color: #FFFFFF;
   text-align: center;
   padding: 0 5px 0 5px;
   background: -webkit-linear-gradient(180deg, #988CD8 18%, #006297 100%);
   border-radius: 13px;
   border: solid 2px #9651ad;
   margin-left: 25px;
}

.bot--botArea-message:after {
   content: '';
   position: absolute;
   border-style: solid;
   border-width: 14px 20px 14px 0;
   border-color: transparent #006297;
   display: block;
   width: 0;
   z-index: 1;
   left: -20px;
   top: 28px;
}

.bot--botArea-message:before {
   content: '';
   position: absolute;
   border-style: solid;
   border-width: 15px 21px 15px 0;
   border-color: transparent #9651ad;
   display: block;
   width: 0;
   z-index: 0;
   left: -23px;
   top: 27px;
}

