.bot--hintArea {
  display: -ms-flexbox;
  display: flex;
  margin-left: 10px;
  font-family: 'Geo', sans-serif;
}

@-webkit-keyframes bot--hintArea-btn {
  25% { -webkit-transform: scale(1.1); transform: scale(1.1); }
  75% { -webkit-transform: scale(0.9); transform: scale(0.9); }
}

@keyframes bot--hintArea-btn {
  25% { -webkit-transform: scale(1.1); transform: scale(1.1); }
  75% { -webkit-transform: scale(0.9); transform: scale(0.9); }
}

.bot--hintArea-btn {
  font-size: 15px;
  background: none;
  outline: none;
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
          transform: perspective(1px) translateZ(0);
  -webkit-box-shadow: 0 0 1px transparent;
          box-shadow: 0 0 1px transparent;
  cursor: pointer;
  font-family: 'Geo', sans-serif;
  font-size: 22px;
}

.bot--hintArea-btn:hover,
.bot--hintArea-btn:active {
  -webkit-animation-name: bot--hintArea-btn;
          animation-name: bot--hintArea-btn;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.bot--hintArea-close {
  display: none;
}

.bot--hintArea-open {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0 0 0 7px;
  margin: 0;
  cursor: w-resize;
}
.bot--hintArea-open span {
  font-family: 'Wendy One', sans-serif;
}

.bot--hintArea-open li:hover {
  background-color: rgb(235, 218, 239);
}

.bot--attention {
  width: 32px;
  height: 25px;
  margin-left: 10px;
  vertical-align: text-bottom;
}

.timer {
  position: fixed;
  top: 50%;
  right: 112px;
}



