.question-area {
  display: -ms-flexbox;
  display: flex;
  width: 1100px;
  border: 2px solid black;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.question-area--container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
}

.question-area--question {
  display: -ms-flexbox;
  display: flex;
  font-size: 43px;
  background: aliceblue;
  -ms-flex-pack: center;
      justify-content: center;
}

.question-area--text {
  text-align: center;
  margin: 0 30px;
}

.question-area--actions {
  display: -ms-flexbox;
  display: flex;
}

.question-area--btn-next {
  width: 54px;
  outline: none;
  cursor: pointer;
}

.question-area--label {
  border-bottom: 1px solid black;
  padding: 15px;
  background-color: rgba(195, 195, 195, 0.50);
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  cursor: pointer;
}

